/* Scroll FX */

.scrollfx {
	transition: all ease-in-out 1s;

	&.scrollfx-fromleft {
		transform: translate3d(-100px, 0, 0);
		
			
		&.activated {
			transform: translate3d(0, 0, 0);
		}

	}
	
	&.scrollfx-fromright {
		transform: translate3d(100px, 0, 0);
	
		&.activated {
			transform: translate3d(0, 0, 0);
		}	
		
	}
	
	&.scrollfx-frombottom {
		transform: translate3d(0px, 100px, 0);
	
		&.activated {
			transform: translate3d(0, 0, 0);
		}	
		
	}
	
	&.scrollfx-fromtop {
		transform: translate3d(0px, -100px, 0);
	
		&.activated {
			transform: translate3d(0, 0, 0);
		}	
		
	}
	
	&.scrollfx-fadein {
		opacity: 0;
	
		&.activated {
			opacity: 1;
		}	
		
	}

}




